

body{
    margin: 0;
    padding: 0;
    height:150vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.page-head{
    color:black;
    font-size: 60px;
    text-align: center;
}

.navbar{
    color:black;
    background-color: aqua;
}

.main-card{
    background-color: antiquewhite;
    width:600px;
    height:700px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 10px;
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    
}

.card-head{
    background-color: rgb(233,120,60);
    width:540px;
    height:40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px 10px 0 0;
}

.card-headline{
    text-align: center;
    color:white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 20px;
}

/* */
.card-body{
    display: flex;
    flex-direction: column;
    gap:20px;
    margin-top: 20px;
}

.cards{
    display: flex;
    align-items: center;
    padding: 10px;
    border:1px solid #ccc;
    border-radius: 10px;
    background-color: aliceblue;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.card-image{
    border-radius: 10px;
    margin-right: 15px;
}

.card-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.card-stats{
    display: flex;
    gap:5px;
    margin-bottom: 10px;
}

.card-title{
    font-size: 18px;
    font-weight: bold;
}

.card-price{
    color: green;
    font-weight: bold;
}